import React, { useState } from 'react';
import { Container, Image, Row, Col } from 'react-bootstrap';
import icon from '../assets/images/profile_icon.jpg';
import authService from '../services/authService';
import useAuth from '../hooks/useAuth';
import assetService from '../services/assetService';
import '../styles/Profile.css';

function Profile() {
    const [user, setUser] = useState([]);
    const [assets, setAssets] = useState([]);
    const [totalvalue, setTotalvalue] = useState([]);

    useAuth(() => {
      const retreiveAssets = async () => {
        const data = await assetService.getAllAssets();
        var totalprice = 0;
        data.forEach(element => {
            totalprice += element.value;
        });
        setTotalvalue(totalprice);
        setAssets(data);
        const retreiveUser = async () => {
          const data = await authService.userDetails();
          setUser(data);
        };
        retreiveUser();
      };
      retreiveAssets();
      const retreiveUser = async () => {
        const data = await authService.userDetails();
        setUser(data);
      };
      retreiveUser();
    });

    return (
        <Container>
          <br></br>
          <h1 className="text-center">My Profile</h1>
          <Row>
          <div class="col-xs-12 col-sm-6 col-md-6">
            <div class="well well-sm">
              <br></br>
              <br></br>
              <br></br>
              <br></br>
                <Row>
                    <Col>
                        {/* We could add ability to edit this picture as well */}
                      <Image src={icon} width="200" height="auto" roundedCircle></Image>
                    </Col>
                    <Col>
                        <h4>{user.firstName} {user.lastName}</h4>
                        {/* We can add location if we want and put it here */}
                        <small><cite title="San Francisco, USA">San Francisco, USA <i class="glyphicon glyphicon-map-marker">
                        </i></cite></small>
                        <p>
                            <i class="glyphicon glyphicon-envelope"></i>{user.email}
                            <br />
                            <i class="glyphicon glyphicon-gift"></i>{user.phone}
                            <br />
                            <br />
                            <i class="glyphicon glyphicon-gift"></i>Assets value: ${totalvalue}
                            <br />
                            <i class="glyphicon glyphicon-gift"></i># of assets: {assets.length}</p>

                            {/* We could add links to other pages they have here if we want
                                or even make this an edit button if we want, just don't know if we need that yet */}
                        <div class="btn-group">
                            <button type="button" class="btn btn-primary">
                                Social</button>
                            <button type="button" class="btn btn-primary dropdown-toggle" data-toggle="dropdown">
                                <span class="caret"></span><span class="sr-only">Social</span>
                            </button>
                            <ul class="dropdown-menu" role="menu">
                                <li><a href="#">Twitter</a></li>
                                <li><a href="https://plus.google.com/+Jquery2dotnet/posts">Google +</a></li>
                                <li><a href="https://www.facebook.com/jquery2dotnet">Facebook</a></li>
                                <li class="divider"></li>
                                <li><a href="#">Github</a></li>
                            </ul>
                        </div>
                    </Col>
                </Row>
            </div>
        </div>
          </Row>
        </Container>
    );
}

export default Profile;
