import React, { useState, useEffect } from 'react';
import Navbar from 'react-bootstrap/Navbar'
import { Form, Button, FormControl, Nav, NavDropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import authService from "../services/authService";
import { useHistory } from 'react-router-dom';
import Profile from '../pages/Profile';

function Header() {

  const [isLoggedIn, setLoggedIn] = useState([]);
  const history = useHistory();

  useEffect(() => {
    const retrieveUser = async () => {
      const data = await authService.isLoggedIn();
      setLoggedIn(data);
    };
    retrieveUser();
  });

  function logout() {
        authService.logout();
        history.push('/');
    }

    return (
        <Navbar bg="dark" variant="dark" expand="lg">
            <div className="container">
                <Navbar.Brand as={Link} to="/">ATP</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link as={Link} to="/">Home</Nav.Link>
                        <Nav.Link as={Link} to="/assets">Assets</Nav.Link>
                        <Nav.Link as={Link} to="/contact">Contact</Nav.Link>
                        <Profile />
                    </Nav>
                    <Form inline>
                        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                    <RightButton />
                </Navbar.Collapse>
            </div>
        </Navbar>
    );

    function RightButton() {
        if(isLoggedIn) {
            return <Button className="ml-2" variant="outline-primary" onClick={ () => {logout()}}>Logout</Button>
        } else {
            return <Button className="ml-2" variant="outline-primary" as={Link} to="/login">Login</Button>
        }
    }

  return (
    <Navbar bg='dark' variant='dark' expand='lg'>
      <div className='container'>
        <Navbar.Brand as={Link} to='/'>
          ATP
        </Navbar.Brand>
        <Navbar.Toggle aria-controls='basic-navbar-nav' />
        <Navbar.Collapse id='basic-navbar-nav'>
          <Nav className='mr-auto'>
            <Nav.Link as={Link} to='/'>
              Home
            </Nav.Link>
            <Nav.Link as={Link} to='/assets'>
              Assets
            </Nav.Link>
            <Nav.Link as={Link} to='/contact'>
                Contact
            </Nav.Link>
            <Nav.Link as={Link} to='/statistics'>
                Stats
            </Nav.Link>
          </Nav>
          <Form inline>
            <FormControl type='text' placeholder='Search' className='mr-sm-2' />
            <Button variant='outline-success'>Search</Button>
          </Form>
          <RightButton />
        </Navbar.Collapse>
      </div>
    </Navbar>
  );


    function Profile() {
        if(isLoggedIn) {
            return <Nav.Link as={Link} to="/profile">Profile</Nav.Link>
        }
        return null;
    }
}

export default Header;
