import React, { useState } from 'react';
import AssetList from '../components/AssetList';
import { Container, Row, Col, Button, FormCheck, FormLabel, InputGroup } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import localStorageService from '../services/localStorageService';
import '../styles/AssetPage.css';

function ViewModeToggle({ viewMode, setViewMode }) {
  const handleViewModeChange = e => {
    localStorageService.assetViewMode = e.target.value;
    setViewMode(e.target.value);
  };

  return (
    <Col>
      <InputGroup>
        <FormLabel className='view-mode-toggle'>
          <FormCheck
            type='radio'
            value='card'
            checked={viewMode === 'card'}
            onChange={handleViewModeChange}
          />
          Card View
        </FormLabel>
        <FormLabel className='view-mode-toggle'>
          <FormCheck
            type='radio'
            value='spreadsheet'
            checked={viewMode === 'spreadsheet'}
            onChange={handleViewModeChange}
          />
          Spreadsheet View
        </FormLabel>
      </InputGroup>
    </Col>
  );
}

function HomePage() {
  const [viewMode, setViewMode] = useState(localStorageService.assetViewMode);

  return (
    <div>
      <Container>
        <br></br>
        <Row>
          <Col>
            <h1>Your Assets:</h1>
          </Col>
          <Col>
            <Button variant='success btn-lg float-right' as={Link} to='/add_asset'>
              New
            </Button>
          </Col>
        </Row>
        <Row>
          <ViewModeToggle viewMode={viewMode} setViewMode={setViewMode} />
        </Row>
        <AssetList viewMode={viewMode} />
      </Container>
    </div>
  );
}

export default HomePage;
