class LocalStorageService {
  constructor() {
    this.baseKey = 'ATP';
  }

  save(key, data) {
    localStorage.setItem(`${this.baseKey}.${key}`, JSON.stringify(data));
  }

  load(key) {
    return JSON.parse(localStorage.getItem(`${this.baseKey}.${key}`));
  }

  logout() {
    this.delete('accessToken');
    this.delete('refreshToken');
  }

  delete(key) {
    localStorage.removeItem(`${this.baseKey}.${key}`);
  }

  get accessToken() {
    return this.load('accessToken');
  }

  set accessToken(data) {
    this.save('accessToken', data);
  }

  get refreshToken() {
    return this.load('refreshToken');
  }

  set refreshToken(data) {
    this.save('refreshToken', data);
  }

  get assetViewMode() {
    const data = this.load('assetViewMode');
    if (data == null) {
      return 'card';
    }

    return data;
  }

  set assetViewMode(value) {
    this.save('assetViewMode', value);
  }
}

const localStorageService = new LocalStorageService();
export default localStorageService;
