import React from 'react';
import { Spinner, Row, Col } from 'react-bootstrap';
import '../styles/Spinner.css';

/**
 *
 * @param {isLoading} isLoading Whether the component should show a loading spinner
 * @param {children} children The components to render if the data is loaded
 */
function LoadingSpinner({ isLoading, children }) {
  if (isLoading) {
    return (
      <Row>
        <Col xs='12' className='d-flex justify-content-center'>
          <Spinner className='loading-spinner' animation='border' variant='primary' role='status' />
        </Col>
      </Row>
    );
  } else {
    return <>{children}</>;
  }
}

export default LoadingSpinner;
