import atpApi from '../atpApi';

class AuthRoutes {
  get baseRoute() {
    return `${atpApi}/auth`;
  }

  register() {
    return `${this.baseRoute}/register`;
  }

  login() {
    return `${this.baseRoute}/login`;
  }

  refresh() {
    return `${this.baseRoute}/refresh`;
  }

  isLoggedIn() {
    return `${this.baseRoute}/isLoggedIn`;
  }

  userDetails() {
    return `${this.baseRoute}/userDetails`;
  }
}

const authRoutes = new AuthRoutes();
export default authRoutes;
