import React from 'react';
import { Card, Button, Col } from 'react-bootstrap';
import truncate from '../../utils/truncate';
import '../../styles/AssetCard.css';
import { Link } from 'react-router-dom';

function AssetCard({ asset }) {
  const description = asset.description
    ? truncate(asset.description, 40)
    : 'No description provided';

  const imageUrl = asset.imageUrl
    ? asset.imageUrl
    : 'https://bhmlib.org/wp-content/themes/cosimo-pro/images/no-image-box.png';

  const linkUrl = "/asset/" + asset.id;

  return (
    <Col md='6' lg='4' className='asset-card'>
      <Card>
        <Card.Img src={imageUrl} width='320' height='170' />
        <Card.Title>{`${asset.name}: $${asset.value}`}</Card.Title>
        <Card.Text>{description}</Card.Text>
        <Button as={Link} to={linkUrl}>
          View Details
        </Button>
      </Card>
    </Col>
  );
}

export default AssetCard;
