import React, { useState } from 'react';
import { Container, Button, InputGroup } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import LoadingSpinner from '../components/LoadingSpinner';
import useAuth from '../hooks/useAuth';
import assetService from '../services/assetService';

function AddAsset() {
  const history = useHistory();
  const { handleSubmit, register } = useForm();
  const [isLoading, setIsLoading] = useState(false);
  const [imageFileName, setImageFileName] = useState(null);

  useAuth(() => {});

  const onSubmit = async data => {
    setIsLoading(true);
    console.log(data.image);
    const success = await assetService.createAsset(
      data.name,
      data.value,
      data.description,
      data.image[0]
    );
    if (success) {
      history.push('/assets');
    } else {
      console.error('Error creating asset');
    }
    setImageFileName(null);
    setIsLoading(false);
  };

  return (
    <LoadingSpinner isLoading={isLoading}>
      <Container>
        <br></br>
        <h1>Add Asset</h1>
        <br></br>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Form.Group controlId='formGroupName'>
            <Form.Label>Name</Form.Label>
            <Form.Control
              type='name'
              name='name'
              placeholder='Enter name'
              ref={register({ required: true })}
            />
          </Form.Group>
          <Form.Group controlId='formGroupPrice'>
            <Form.Label>Price</Form.Label>
            <InputGroup>
              <InputGroup.Prepend>
                <InputGroup.Text id='inputGroupPrepend'>$</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                type='number'
                step='any'
                name='value'
                placeholder='Enter price'
                ref={register({ required: true })}
              />
            </InputGroup>
          </Form.Group>
          <Form.Group controlId='formGroupDescription'>
            <Form.Label>Description</Form.Label>
            <Form.Control
              type='description'
              placeholder='Enter description'
              name='description'
              ref={register}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Image (optional)</Form.Label>
            <Form.File
              id='custom-file'
              label={imageFileName ? imageFileName : 'File upload'}
              name='image'
              accept='image/png, image/jpeg'
              ref={register}
              custom
              onChange={e => setImageFileName(e.target.files[0].name)}
            />
          </Form.Group>
          <Button variant='success' type='submit'>
            Create
          </Button>
        </Form>
      </Container>
    </LoadingSpinner>
  );
}

export default AddAsset;
