import React, { useState } from 'react';
import AssetCard from '../AssetCard';
import AssetEntry from '../AssetEntry';
import { Row, Col } from 'react-bootstrap';
import useAuth from '../../hooks/useAuth';
import assetService from '../../services/assetService';
import LoadingSpinner from '../LoadingSpinner';
import '../../styles/AssetList.css';

function NoAssetsNotice() {
  return (
    <Row>
      <Col xs='12' className='no-assets-notice'>
        <h3>You have not added any assets.</h3>
      </Col>
    </Row>
  );
}

function CardView({ assets }) {
  if (assets.length > 0) {
    return (
      <Row>
        {assets.map(item => {
          return <AssetCard key={item.id} asset={item} />;
        })}
      </Row>
    );
  } else {
    return <NoAssetsNotice />;
  }
}

function SpreadsheetView({ assets }) {
  if (assets.length > 0) {
    return (
      <div className='asset-spreadsheet'>
        <div className='asset-spreadsheet-header'>
          <Col>Name</Col>
          <Col>Value</Col>
          <Col>Description</Col>
          <Col>Details</Col>
        </div>
        {assets.map(item => {
          return <AssetEntry key={item.id} asset={item} />;
        })}
      </div>
    );
  } else {
    return <NoAssetsNotice />;
  }
}

function AssetList({ viewMode }) {
  const [assets, setAssets] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useAuth(() => {
    const retreiveAssets = async () => {
      const data = await assetService.getAllAssets();
      setAssets(data);
      setIsLoading(false);
    };
    retreiveAssets();
  });

  return (
    <LoadingSpinner isLoading={isLoading}>
      {viewMode === 'spreadsheet' ? (
        <SpreadsheetView assets={assets} />
      ) : (
        <CardView assets={assets} />
      )}
    </LoadingSpinner>
  );
}

export default AssetList;
