import axios from 'axios';
import assetRoutes from './routes';
import authService from '../authService';

class AssetService {
  async getAllAssets() {
    try {
      const res = await axios.get(assetRoutes.baseRoute, { headers: authService.authHeaders });
      return res.data.assets;
    } catch (error) {
      console.error(error);
      return null;
    }
  }

  async getSingleAsset(id) {
    try {
      const res = await axios.get(assetRoutes.singleAsset(id), {
        headers: authService.authHeaders
      });
      return res.data;
    } catch (error) {
      console.error(error);
      return null;
    }
  }

  async createAsset(name, value, description, image) {
    try {
      const res = await axios.post(
        assetRoutes.baseRoute,
        {
          name,
          value,
          description
        },
        {
          headers: authService.authHeaders
        }
      );
      const id = res.data.id;
      const formData = new FormData();
      formData.append('image', image);
      await axios.post(assetRoutes.assetImage(id), formData, { headers: authService.authHeaders });
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }

  async updateAsset(id, name = null, value = null, description = null) {
    try {
      await axios.patch(
        assetRoutes.singleAsset(id),
        {
          name,
          value,
          description
        },
        {
          headers: authService.authHeaders
        }
      );
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }

  async deleteAsset(id) {
    try {
      await axios.delete(assetRoutes.singleAsset(id), { headers: authService.authHeaders });
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }
}

const assetService = new AssetService();
export default assetService;
