import atpApi from '../atpApi';

class AssetRoutes {
  get baseRoute() {
    return `${atpApi}/asset`;
  }

  singleAsset(assetId) {
    return `${this.baseRoute}/${assetId}`;
  }

  assetImage(assetId) {
    return `${this.baseRoute}/${assetId}/image`;
  }
}

const assetRoutes = new AssetRoutes();
export default assetRoutes;
