import React, { useState } from 'react';

function ContactForm() {

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
    });

    function handleSubmit(e) {
        var payload = { formData }.formData.name + "\n" +
            { formData }.formData.email + "\n" +
            { formData }.formData.message;
        alert("STATE DATA:\n" + payload);
    };

    return (
        <form id="contact-form" onSubmit={handleSubmit} method="POST">
            <div className="form-group">
                <label htmlFor="name">Name</label>
                <input type="text" className="form-control" value={formData.name} onChange={(e) => setFormData({ ...formData, name: e.target.value })} />
            </div>
            <div className="form-group">
                <label htmlFor="exampleInputEmail1">Email address</label>
                <input type="email" className="form-control" value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })} />
            </div>
            <div className="form-group">
                <label htmlFor="message">Message</label>
                <textarea className="form-control" rows="5" value={formData.message} onChange={(e) => setFormData({ ...formData, message: e.target.value })} />
            </div>
            <button type="submit" className="btn btn-primary btn-block">Submit</button>
        </form>
    );
}

export default ContactForm;