import axios from 'axios';
import authRoutes from './routes';
import localStorageService from '../localStorageService';
import jwt from 'jsonwebtoken';

class AuthService {
  get accessToken() {
    return localStorageService.accessToken;
  }

  get isAccessTokenExpired() {
    if (this.accessToken) {
      const { exp } = jwt.decode(this.accessToken);
      const expirationTime = new Date(exp * 1000);
      const now = new Date();
      return expirationTime - now <= 0;
    }

    return true;
  }

  get refreshToken() {
    return localStorageService.refreshToken;
  }

  set accessToken(value) {
    localStorageService.accessToken = value;
  }

  set refreshToken(value) {
    localStorageService.refreshToken = value;
  }

  get authHeaders() {
    return { Authorization: `Bearer ${this.accessToken}` };
  }

  async register(email, password, firstName, lastName, phone) {
    try {
      await axios.post(authRoutes.register(), {
        email,
        password,
        firstName,
        lastName,
        phone
      });
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }

  async logout() {
    localStorageService.logout();
  }

  async login(email, password) {
    try {
      const res = await axios.post(authRoutes.login(), {
        email,
        password
      });
      this.accessToken = res.data.token;
      this.refreshToken = res.data.refresh;
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }

  async refresh() {
    try {
      const res = await axios.post(authRoutes.refresh(), {
        refresh: this.refreshToken
      });
      this.accessToken = res.data.token;
      this.refreshToken = res.data.refresh;
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  }

  async isLoggedIn(tryRefresh = true) {
    try {
      await axios.get(authRoutes.isLoggedIn(), {
        headers: {
          Authorization: `Bearer ${this.accessToken}`
        }
      });
      return true;
    } catch (error) {
      if (tryRefresh) {
        const refreshSucceded = await this.refresh();
        if (refreshSucceded) {
          return await this.isLoggedIn(false);
        } else {
          return false;
        }
      } else {
        return false;
      }
    }
  }

  async userDetails() {
    try {
      const res = await axios.get(authRoutes.userDetails(), {
        headers: this.authHeaders
      });
      return {
        email: res.data.email,
        firstName: res.data.firstName,
        lastName: res.data.lastName,
        phone: res.data.phone
      };
    } catch (error) {
      return null;
    }
  }
}

const authService = new AuthService();
export default authService;
