import { useParams, useHistory } from "react-router-dom";
import React, { useState } from 'react';
import assetService from '../services/assetService';
import LoadingSpinner from '../components/LoadingSpinner';
import useAuth from '../hooks/useAuth';
import { Container, Image, Button, InputGroup } from "react-bootstrap";
import Form from 'react-bootstrap/Form';
import { useForm } from 'react-hook-form';


function AssetPage() {

    let { id } = useParams();
    const history = useHistory();
    const [asset, setAsset] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [showForm, setShowForm] = useState(false);
    const { handleSubmit, register } = useForm();
    // const [imageFileName, setImageFileName] = useState(null);

    useAuth(() => {
        const retreiveAsset = async () => {
          const data = await assetService.getSingleAsset(id);
          setAsset(data);
          setIsLoading(false);
        };
        retreiveAsset();
      });

    const imageUrl = asset.imageUrl
        ? asset.imageUrl
        : 'https://bhmlib.org/wp-content/themes/cosimo-pro/images/no-image-box.png';

    async function deleteAsset() {
        const deleted = await assetService.deleteAsset(id);
        if(deleted) {
            history.push('/assets');
        } else {
            alert("Error Deleting Asset");
        }
      }

    function openCloseForm() {
        if (showForm) {
            setShowForm(false);
        } else {
            setShowForm(true);
        }
      }

    const onSubmit = async data => {
        setIsLoading(true);
        const success = await assetService.updateAsset(
          id,
          data.name,
          data.value,
          data.description
        );
        if (success) {
          history.push('/assets');
        } else {
          console.error('Error creating asset');
        }
        setIsLoading(false);
      };

    const EditForm = () => (
        <div>
        <br></br>
        <h1>Edit Asset</h1>
        <Form onSubmit={handleSubmit(onSubmit)}>
        <Form.Group controlId='formGroupName'>
            <Form.Label>Name</Form.Label>
            <Form.Control
              type='name'
              defaultValue={asset.name}
              name='name'
              placeholder='Enter name'
              ref={register({ required: true })}
            />
          </Form.Group>
          <Form.Group controlId='formGroupPrice'>
            <Form.Label>Price</Form.Label>
            <InputGroup>
              <InputGroup.Prepend>
                <InputGroup.Text id='inputGroupPrepend'>$</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                type='number'
                defaultValue={asset.value}
                step='any'
                name='value'
                placeholder='Enter price'
                ref={register({ required: true })}
              />
            </InputGroup>
          </Form.Group>
          <Form.Group controlId='formGroupDescription'>
            <Form.Label>Description</Form.Label>
            <Form.Control
              type='description'
              defaultValue={asset.description}
              placeholder='Enter description'
              name='description'
              ref={register}
            />
          </Form.Group>
          {/* This can be used in the future to edit image */}
          {/* <Form.Group>
            <Form.Label>Image (optional)</Form.Label>
            <Form.File
              id='custom-file'
              label={imageFileName ? imageFileName : 'File upload'}
              name='image'
              accept='image/png, image/jpeg'
              ref={register}
              custom
              onChange={e => setImageFileName(e.target.files[0].name)}
            />
          </Form.Group> */}
          <Button variant='success' type='submit'>
            Submit
          </Button>
        </Form>
        </div>
    );

    return (
        <LoadingSpinner isLoading={isLoading}>
            <Container>
                <br></br>
                <h1>Edit Asset Page</h1>
                <br></br>
                <h2>{asset.name}</h2>
                <Image src={imageUrl} width='auto' height='250'></Image>
                <br></br>
                <br></br>
                <h5>Value: ${asset.value}</h5>
                <h5>Description:</h5>
                <p>{asset.description}</p>
                <Button className="mr-2" onClick={() => openCloseForm()}>Edit</Button>
                <Button className="btn-danger" onClick={() => { if (window.confirm('Are you sure you wish to delete this item?')) deleteAsset() } }>Delete</Button>
                { showForm ? <EditForm /> : null }
            </Container>
        </LoadingSpinner>
    );
}

export default AssetPage;