import React from 'react';
import Header from './components/Header';
import HomePage from './pages/HomePage';
import CompanyPage from './pages/CompanyPage';
import ContactPage from './pages/ContactPage';
import LoginPage from './pages/LoginPage';
import Profile from './pages/Profile';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import AddAsset from './pages/AddAsset';
import Footer from './components/Footer';
import AssetPage from './pages/AssetPage';
import StatsPage from './pages/StatsPage';
import './styles/App.css';

function App() {
  return (
    <div className='app'>
      <Router>
        <Header />
        <main className='content'>
          <Switch>
            <Route path='/assets'>
              <Assets />
            </Route>
            <Route path='/statistics'>
                <StatsPage />
            </Route>
            <Route path='/add_asset'>
              <AddAsset />
            </Route>
            <Route path='/page1'>
              <Page1 />
            </Route>
            <Route path='/page2'>
              <Page2 />
            </Route>
            <Route path='/page3'>
              <Page3 />
            </Route>
            <Route path='/contact'>
              <Contact />
            </Route>
            <Route path='/login'>
              <LoginPage />
            </Route>
            <Route path='/profile'>
              <Profile />
            </Route>
            <Route path='/asset/:id'>
                <AssetPage />
            </Route>
            <Route path='/'>
              <Home />
            </Route>
          </Switch>
        </main>
        <Footer />
      </Router>
    </div>
  );
}

function Home() {
  return <CompanyPage />;
}

function Assets() {
  return <HomePage />;
}

function Page1() {
  return <h2>Page1</h2>;
}

function Page2() {
  return <h2>Page2</h2>;
}

function Page3() {
  return <h2>Page3</h2>;
}

function Contact() {
  return <ContactPage />;
}

function Login() {
  return <LoginPage />;
}

function Stats() {
    return <StatsPage />;
}

export default App;
